export default {

    success: function(self, msg){
        self.$notify({
            title: '',
            message: msg,
            type: 'success'
        });
    },

    error: function(self, msg){
        self.$notify.error({
            title: '',
            message: msg
        });
    },

    confirm: function(self, callBack, msg = "?"){
        self.$confirm(msg, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(() => {
            callBack()
        }).catch(() => {

        });
    },

}


